import Vue from 'vue'
import App from './App.vue'
import VuePaginate from 'vue-paginate'
import router from './router'
import './assets/tailwind.css'
import VueAgile from 'vue-agile'
import store from "./store";
Vue.use(VueAgile)
Vue.use(VuePaginate)
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
  store
}).$mount('#app')
