<template lang="pug">
    .icons.grid.grid-cols-3.gap-4.w-full.mt-8.bg-picture-fondo2(class="lg:grid-cols-6 lg:gap-0")
        .icono.text-blue-400.border-b-4.border_blue_bottom(@click="clicked('i-p-1')" id="i-p-1")
            object.svg(type="image/svg+xml" data="https://pictureperu.com/web/iconos/A-ALL.svg")
            span.text-sm Todos
        .icono(@click="clicked('i-p-2')" class="" id="i-p-2")
            object.svg(type="image/svg+xml" data="https://pictureperu.com/web/iconos/B-PHOTOBOOKS.svg")
            span.text-sm Photobooks
        .icono(@click="clicked('i-p-3')" class="" id="i-p-3")
            object.svg(type="image/svg+xml" data="https://pictureperu.com/web/iconos/C-FOTO%20ALBUM.svg")
            span.text-sm Foto Album
        .icono(@click="clicked('i-p-4')" class="" id="i-p-4")
            object.svg(type="image/svg+xml" data="https://pictureperu.com/web/iconos/D-HOME%20DECOR.svg")
            span.text-sm Home Decor
        .icono(@click="clicked('i-p-5')" class="" id="i-p-5")
            object.svg(type="image/svg+xml" data="https://pictureperu.com/web/iconos/E-IMPRESIONES.svg")
            span.text-sm Impresiones
        .icono(@click="clicked('i-p-6')" class="" id="i-p-6")
            object.svg(type="image/svg+xml" data="https://pictureperu.com/web/iconos/F-GIFT%20BOX.svg")
            span.text-sm Gift Box
</template>

<script>
    export default {
        name: "IconPromos",
        data(){
            return {
                anterior: 'i-p-1',
                showall: (window.innerWidth >= 1024)
            }
        },
        methods:{
            clicked(idClickeado){
                let elementoAnterior = document.getElementById(this.anterior);
                elementoAnterior.classList.remove("text-blue-400");
                elementoAnterior.classList.remove("border-b-4");
                elementoAnterior.classList.remove("border_blue_bottom");
                elementoAnterior.classList.add("text-picture-texto");
                let clickedElement = document.getElementById(idClickeado);
                clickedElement.classList.remove("text-picture-texto");
                clickedElement.classList.add("text-blue-400");
                clickedElement.classList.add("border-b-4");
                clickedElement.classList.add("border_blue_bottom");
                this.anterior = idClickeado;
            }
        }
    }
</script>

<style lang="sass" scoped>
    .border_blue_bottom
        border-bottom-color: white
    .svg
        width: 50px
        height: 50px
    .icono
        display: flex
        justify-content: center
        align-items: center
        flex-direction: column
</style>