<template lang="pug">
    .iconText.bg-red-500.flex.items-center.justify-center(@mouseover="showPedir = true" @mouseleave="showPedir = false" :class="{'bg-blue-200' : showPedir === true}")
        i(v-show="showPedir === false" :class="typeIcon + ' ' + icon")
        div.flex.flex-col(v-show="showPedir === true")
            span.text-xs.font-hairline {{texto1}}
            span.text-sm.font-light {{texto2}}

</template>

<script>
    export default {
        name: "IconoText",
        props:{
            texto1: String,
            texto2: String,
            typeIcon: String,
            icon: String
        },
        data(){
            return {
                showPedir: false
            }
        },
    }
</script>

