<template lang="pug">
    div.flex.flex-col(class="lg:flex-row")
        design-categories.bg-picture-fondo2(class="lg:w-2/12" @designFilter="setShowData")
        div.mt-2(class="lg:w-10/12")
            formats-product.bg-picture-fondo2.mb-1
            div
                paginate(name="filterData" :list="filterData" :per="6" tag="div")
                    div.grid(class="lg:grid-cols-2")
                        span(v-for="design in paginated('filterData')")
                            card-diseno(:img="design.img_principal")
                paginate-links.flex.justify-around.items-center(for="filterData" :hide-single-page="true" :limit="3" :show-step-links="true" :classes="{'.active' : 'text-green-300', 'li':['cursor-pointer','text-lg', 'text-blue']}")

</template>

<script>
    import CardDiseno from "./CardDiseno";
    import DesignCategories from "./DesignCategories";
    import FormatsProduct from "./FormatsProduct";
    import api from "../../api";
    export default {
        name: "DesignContainer",
        components: {FormatsProduct, DesignCategories, CardDiseno},
        async created() {
            const subParsed = this.$route.params.subcategoria.toUpperCase().replace('-', ' ');
            const id_subcat = await api.getSubCatProduct(null, null, subParsed);
            this.products = await  api.getProduct(null, id_subcat[0].id);
            this.showProduct = this.products[0].id;
            this.originalData = await api.getImgDesign(null, this.showProduct);
            this.originalData = api.shuffle(this.originalData);
            this.showDesign = 0;
        },
        data(){
            return {
                originalData: [],
                products: [],
                showDesign : null,
                showProduct: null,
                paginate: ['filterData']
            }
        },
        methods:{
            setShowData(id){
                this.showDesign = id;
            }
        },
        computed:{
            filterData(){
                if (this.showDesign === 0){
                    return this.originalData;
                }
                else{
                    return this.originalData.filter(a => a.catdiseno.id === this.showDesign)
                }
            }
        }
    }
</script>

<style lang="sass" scoped>
    ul.paginate-links > li.active > a
        color: green
</style>