import Vue from 'vue'
import VueRouter from 'vue-router'
import ProductWithDesign from "../views/ProductWithDesign";
import Promociones from "../views/Promociones";
import ProductWithCards from "../views/ProductWithCards";
import Home from "../views/Home";
import ProductDetail from "../views/ProductDetail";

Vue.use(VueRouter)

  const routes = [
      {
        path: '/giftbox',
        name: 'rutaGiftBox',
        component: ProductWithCards
      },
      {
          path: '/:categoria-:subcategoria-d',
          name: 'rutaGeneralProductosConDiseño',
          component: ProductWithDesign
      },
      {
          path: '/:categoria-:subcategoria-c',
          name: 'rutaGeneralProductosConCards',
          component: ProductWithCards
      },
      {
        path: '/promociones',
        name: 'Promociones',
        component: Promociones
      },
      {
          path: '/details',
          name: 'Product Detail',
          component: ProductDetail
      },
      {
        path: '/',
        name: 'Home',
        component: Home
      }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
