const apiUrl = 'https://ecommerce-backend-pc.herokuapp.com/graphql';

function getRequestOption(body){
    const myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    return {
        method: 'POST',
        headers: myHeaders,
        body: body,
        redirect: 'follow'
    }
}
function shuffle(array) {
    let m = array.length, t, i;
    while (m) {
        i = Math.floor(Math.random() * m--);
        t = array[m];
        array[m] = array[i];
        array[i] = t;
    }
    return array;
}
function getCatDesign(id){
    if (id != null){
        return fetch(apiUrl, getRequestOption(JSON.stringify({ query: `query{catdiseno(id:${id}){nombre}}`  })))
            .then(res=>res.json())
            .then((res) => res.data.catdiseno)
    }
    return fetch(apiUrl, getRequestOption(JSON.stringify({ query: "query{catdiseno{nombre, id} }"})))
        .then(res=>res.json())
        .then(res => res.data.catdiseno)
}
function getCatProduct(id){
    if (id != null){
        return fetch(apiUrl, getRequestOption(JSON.stringify({ query: `query{categoria(id:${id}){nombre}}`  })))
            .then(res=>res.json())
            .then((res) => res.data)
    }
    return fetch(apiUrl, getRequestOption(JSON.stringify({ query: "query{categoria{nombre, id} }"})))
        .then(res=>res.json())
        .then(res => res.data)
}
function getSubCatProduct(id, cat_id, nombre){
    if (id != null){
        return fetch(apiUrl, getRequestOption(JSON.stringify({ query: `query{subcategoria(id:${id}){id, nombre, img_principal, img_secundaria}}`  })))
            .then(res=>res.json())
            .then((res) => res.data.subcategoria)
    }
    if (nombre != null){
        return fetch(apiUrl, getRequestOption(JSON.stringify({ query: `query{subcategoria(nombre:"${nombre}"){id}}`  })))
            .then(res=>res.json())
            .then((res) => res.data.subcategoria)
    }
    if (cat_id != null){
        return fetch(apiUrl, getRequestOption(JSON.stringify({ query: `query{subcategoria(categoria_id:${cat_id}){nombre}}`  })))
            .then(res=>res.json())
            .then((res) => res.data.subcategoria)
    }
    return fetch(apiUrl, getRequestOption(JSON.stringify({ query: "query{subcategoria{id, nombre, img_principal, img_secundaria} }"})))
        .then(res=>res.json())
        .then(res => res.data)
}
function getImgDesign(id, id_product){
    if (id != null){
        return fetch(apiUrl, getRequestOption(JSON.stringify({ query: `query{img_dis_producto(id:${id}){img_disenos, img_principal, es_principal, catdiseno{id}}}`  })))
            .then(res=>res.json())
            .then((res) => res.data.img_dis_producto)
    }
    if (id_product != null){
        return fetch(apiUrl, getRequestOption(JSON.stringify({ query: `query{img_dis_producto(id_producto:${id_product}){img_disenos, img_principal, es_principal, catdiseno{id}}}`  })))
            .then(res=>res.json())
            .then((res) => res.data.img_dis_producto)
    }
    return fetch(apiUrl, getRequestOption(JSON.stringify({ query: "query{img_dis_producto{img_disenos, img_principal, es_principal, catdiseno{id}}}"})))
        .then(res=>res.json())
        .then(res => res.data.img_dis_producto)
}
function getProduct(id, subcat_id){
    if (id != null){
        return fetch(apiUrl, getRequestOption(JSON.stringify({ query: `query{producto(id:${id}){id,nombre}}`  })))
            .then(res=>res.json())
            .then((res) => res.data.producto)
    }
    if (subcat_id != null){
        return fetch(apiUrl, getRequestOption(JSON.stringify({ query: `query{producto(id_subcategoria:${subcat_id}){id,nombre}}`  })))
            .then(res=>res.json())
            .then(res => res.data.producto)
    }
    return fetch(apiUrl, getRequestOption(JSON.stringify({ query: "query{producto{id,nombre}}"})))
        .then(res=>res.json())
        .then(res => res.data.producto)
}
export default {
    getCatDesign,
    getCatProduct,
    getSubCatProduct,
    shuffle,
    getImgDesign,
    getProduct
}