<template lang="pug">
    div.flex.justify-center.items-center.flex-col(class="lg:justify-start")
        h3.text-xl(class="lg:my-2") CATEGORÍA
        p.border-2.border-blue-400.rounded-full.cursor-pointer(v-show="showall === false" @click="showall = true" class="lg:hidden lg:border-0 w-10/12") {{ elegido }}
            i.fal.fa-chevron-down.ml-4
        ul.border-2.border-blue-400.rounded-lg.flex.flex-col(v-show="showall === true || this.$store.state.screenWidth >= 1024" class="lg:border-0 w-10/12 lg:w-full")
            li.order-1(@click="changeSelected(0)" id="d-0" :class="(selected === 'd-0') ? 'text-blue-400' : 'text-black'") Todos
            li.order-2(v-for="categoria in catDiseno" :class="(selected === 'd-' + categoria.id) ? 'text-blue-400' : 'text-black'" @click="changeSelected(categoria.id)" :id="'d-' + categoria.id") {{categoria.nombre}}
</template>

<script>
    import api from "../../api";
    import {mapState} from "vuex";
    export default {
        name: "DesignCategories",
        async created(){
            if(!this.$store.state.catDiseno){
                const cat = await api.getCatDesign(null);
                this.$store.commit('chargeCatDiseno', {cat: cat})
            }
        },
        computed: {
            ...mapState(['catDiseno', 'screenWidth'])
        },
        data(){
            return {
                selected: 'd-0',
                showall: true,
                elegido: ''
            }
        },
        methods:{
            changeSelected(selected){
                this.selected = 'd-' + selected;
                if(this.$store.state.screenWidth < 1024)
                    this.showall = false;
                this.elegido = document.getElementById(this.selected).innerText;
                this.$emit('designFilter', selected);
            }
        }
    }
</script>

<style lang="sass" scoped>
    ul
        li
           cursor: pointer
</style>