<template lang="pug">
    .formats.flex.flex-col.py-5.justify-center.items-center(class="lg:flex-row")
        h3.mr-3.mb-4.text-xl(class="lg:mb-0") FORMATO
        .icons.grid.grid-cols-3.gap-4.w-full(class="lg:flex")
            .icono.text-blue-400(@click="clicked('f-1')" class="lg:border-l lg:border-gray-400 lg:px-4 lg:mx-2" id="f-1")
                p.text-xl icono
                span.text-sm Lorem
</template>

<script>
    export default {
        name: "FormatsProduct",
        data(){
            return {
                anterior: 'f-1',
                showall: (window.innerWidth >= 1024)
            }
        },
        methods:{
            clicked(idClickeado){
                let elementoAnterior = document.getElementById(this.anterior);
                elementoAnterior.classList.remove("text-blue-400");
                elementoAnterior.classList.add("text-black");
                let clickedElement = document.getElementById(idClickeado);
                clickedElement.classList.remove("text-black");
                clickedElement.classList.add("text-blue-400");
                this.anterior = idClickeado;
            }
        }
    }
</script>

