<template lang="pug">
    button.uppercase.rounded-full.outline-none(:class="stringClass") {{texto1}}
</template>

<script>
    export default {
        props:{
            texto1: String,
            mainColor: String,
            hoverColor: String,
            px: String,
            py: String,
            fontSize: String,
            hoverFontColor: String,
            borderSize: String,
            fontColor: {
                type: String,
                default: "white"
            },
        },
        name: "BtnHover",
        data(){
            return {
                color: '',
                stringClass: 'bg-'+ this.mainColor +
                             ' text-' + this.fontColor +
                             ' px-' + this.px +
                             ' py-' + this.py +
                             ' text-' + this.fontSize
            }
        }
    }
</script>

<style lang="sass" scoped>
</style>