<template lang="pug">
    //:style="'background-image: url('+this.image+');'"
    .homeImportantProduct.relative.w-full.h-full.bg-center.bg-cover.bg-no-repeat(:class="{'opacidad' : showBtn === true}" :style="'background-image: url('+this.image+');'" @mouseover="showBtn = true" @mouseleave="showBtn = false")
        h3.float-left.absolute.top-0.left-0.ml-3.mt-3.font-semibold.text-lg {{this.titulo}}
        //btn-hover.absolute.right-0.bottom-0.mr-4.mb-4(v-show="showBtn === true" texto1="ver más" main-color="blue-400" hover-color="white" px="10" py="1" font-size="sm" border-size="2" hover-font-color="black")
</template>

<script>
    import BtnHover from "../celulas/BtnHover";
    export default {
        components: {BtnHover},
        props:{
            image: String,
            titulo: String
        },
        name: "HoverCardUnderline",
        data(){
            return {
                showBtn: false
            }
        }
    }
</script>

<style lang="sass" scoped>
    .opacidad
        opacity: 90%
    h3
        position: relative
        color: #d68477
        text-decoration: none
    h3:hover
        color: #d68477
    h3::before
        content: ""
        position: absolute
        width: 100%
        height: 2px
        bottom: 0
        left: 0
        background-color: #d68477
        visibility: hidden
        transform: scaleX(0)
        transition: all 0.3s ease-in-out 0s
    .homeImportantProduct:hover > h3::before
        visibility: visible
        transform: scaleX(1)
</style>