<template lang="pug">
    div
        .flex.flex-col(class="lg:flex-row")
            div.px-8.py-4.uppercase.bg-picture-fondo2.cursor-pointer(@click="$refs.carousel.goTo(0)" id="c-p-d-0") Saber Mas
            div.px-8.py-4.uppercase.cursor-pointer.bg-blue-400(@click="$refs.carousel.goTo(1)" id="c-p-d-1") ¿Como hago mi pedido?
            div.px-8.py-4.uppercase.cursor-pointer.bg-blue-400(@click="$refs.carousel.goTo(2)" id="c-p-d-2") Un Tercero

        agile.bg-picture-fondo2(ref="carousel" @before-change="showEvent($event)" :dots="false" :autoplay="false" :infinite="false" :nav-buttons="false" :mobile-first="true")
            .slide
                .flex.flex-col(class="lg:flex-row")
                    div.flex.flex-col.items-start.p-4(class="lg:w-1/2")
                        h4.capitalize.text-left.text-md {{this.categoria}}
                        h3.capitalize.text-left.text-2xl.font-bold.mb-4 {{this.subcategoria}}
                        p.text-left.mb-2 Lorem ipsum dolor sit amet, consectetur adipisicing elit. Architecto asperiores cumque explicabo harum libero officiis quia, unde! Cumque, delectus deserunt eum ex ipsum, iusto laudantium maxime modi, mollitia reiciendis voluptatum?
                        p.text-left.mb-4 Lorem ipsum dolor sit amet, consectetur adipisicing elit. Alias aperiam culpa deserunt dolores earum in, laboriosam non possimus repellat sed? Adipisci, deleniti dolore eos nulla officia qui quis quisquam velit?
                        btn-hover.mt-4(texto1="compra ya!" main-color="blue-400" hover-color="white" px="10" py="1" font-size="sm" border-size="none" hover-font-color="black" font-color="white")
                    div.flex.justify-center.items-center.py-2(class="lg:w-1/2")
                        img(:src="this.img")
            .slide
                .flex()
                    p.text-6xl aca va como hago mi pedido, no se como lo quieres por eso lo dejo asi :v
            .slide
                .flex()
                    p.text-6xl aca va una tercera cosa que no me acuerdo, no se como lo quieres por eso lo dejo asi :v

</template>

<script>
    import BtnHover from "../celulas/BtnHover";
    export default {
        name: "CarouselProductDescriptionTwo",
        components: {BtnHover},
        props: ['categoria', 'subcategoria', 'img'],
        methods:{
            showEvent(event){
                let actual = document.getElementById('c-p-d-' + event.currentSlide);
                let nuevo = document.getElementById('c-p-d-' + event.nextSlide);
                actual.classList.remove("bg-blue-400");
                actual.classList.add("bg-picture-fondo2");
                nuevo.classList.remove("bg-picture-fondo2");
                nuevo.classList.add("bg-blue-400");
            }
        }
    }
</script>

