<template lang="pug">
    .productWidthDesign
        product-description-one(:titulo="tituloPrincipal" :img="this.data_subcat.img_principal").bg-picture-fondo2
        design-container.mt-8
        carousel-product-description-two(:categoria="this.$route.params.categoria" :subcategoria="this.$route.params.subcategoria" :img="this.data_subcat.img_secundaria").mt-6

</template>

<script>
    import ProductDescriptionOne from "../components/organos/ProductDescriptionOne";
    import DesignContainer from "../components/organos/DesignContainer";
    import CarouselProductDescriptionTwo from "../components/organos/CarouselProductDescriptionTwo";
    import api from "../api";
    export default {
        name: "ProductWithDesign",
        async created(){
            const subParsed = this.$route.params.subcategoria.toUpperCase().replace('-', ' ');
            const id_subcat = await api.getSubCatProduct(null, null, subParsed);
            this.data_subcat = await api.getSubCatProduct(id_subcat[0].id, null);
            this.data_subcat = this.data_subcat[0];
        },
        data(){
            return {
                data_subcat: {img_principal: null, img_secundaria: null},
            }
        },
        components: {
            CarouselProductDescriptionTwo, DesignContainer, ProductDescriptionOne
        },
        computed: {
            tituloPrincipal(){
                const paramsRoute = this.$route.params;
                if(paramsRoute.categoria === 'decor'){
                    return `${paramsRoute.subcategoria.replace('-', ' ')}`
                }
                return `${paramsRoute.categoria} ${paramsRoute.subcategoria.replace('-', ' ')}`;
            }
        }
    }
</script>

