import Vue from 'vue'
import Vuex from 'vuex'
 Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        showedProduct: 1,
        showedMenu: 0,
        screenWidth: window.innerWidth,
        showOptions: false,
        catDiseno: null
    },
    mutations: {
        toggleMenu(state) {
            state.showedMenu = ++state.showedMenu % 2;
        },
        hideNavBar(state){
            state.showedMenu = 0;
            state.showedProduct = 1;
        },
        showProduct(state, payload){
            state.showedProduct = payload.idProduct;
        },
        setScreenWidth(state){
            state.screenWidth = window.innerWidth;
        },
        toggleShowOptions(state){
            state.showOptions = !state.showOptions;
        },
        chargeCatDiseno(state, payload){
            state.catDiseno = payload.cat;
        }
    },
})

export default store