                        img.logo(src="https://pictureperu.com/web/home/logo.png")
                    i.fas.fa-bars.font-normal.text-selectedMenu(class="lg:hidden" @click="toggleShowOptions")
                ul.flex.flex-col.w-full.items-center(class="lg:flex-row justify-around" v-if="screenWidth >= 1024 || showOptions")
                    li.bg-gray-500.uppercase.w-full(class="lg:bg-transparent")
                        span.cursor-pointer(@click="toggleMenu") PRODUCTOS
                    ul.w-full(v-show="showedMenu === 1" class="lg:hidden")
                        li.flex.flex-col(@click="setProduct(1)")
                            span.bg-menu(:class="showedProduct === 1 ? 'bg-selectedMenu' : ''") Photobooks
                            ul.bg-gray-300.w-full(v-show="showedProduct === 1" )
                                li(@click="hideNavBar")
                                    router-link(to="/photobook-clasico-d") Clasico
                                li(@click="hideNavBar")
                                    router-link(to="/photobook-tradicional-d") Tradicional
                                li(@click="hideNavBar")
                                    router-link(to="/photobook-premium-d") Premium
                                li(@click="hideNavBar")
                                    router-link(to="/photobook-deluxe-d") Deluxe
                        li.flex.flex-col(@click="setProduct(2)")
                            span.bg-menu(:class="showedProduct === 2 ? 'bg-selectedMenu' : ''") Foto Album
                            ul.bg-gray-300.w-full(v-show="showedProduct === 2")
                                li(@click="hideNavBar")
                                    router-link(to="/album-anillado-d") Anillado
                                li(@click="hideNavBar")
                                    router-link(to="/album-tapa-dura-d") Tapa Dura
                        li.flex.flex-col(@click="setProduct(3)")
                            span.bg-menu(:class="showedProduct === 3 ? 'bg-selectedMenu' : ''") Home Decor
                            ul.bg-gray-300.w-full(v-show="showedProduct === 3")
                                li(@click="hideNavBar")
                                    router-link(to="/decor-tazas-d") Tazas
                                li(@click="hideNavBar")
                                    router-link(to="/decor-foto-cuadros-d") Foto Cuadros
                                li(@click="hideNavBar")
                                    router-link(to="/decor-foto-calendarios-d") Foto Calendarios
                        li.flex.flex-col(@click="setProduct(4)")
                            span.bg-menu(:class="showedProduct === 4 ? 'bg-selectedMenu' : ''") Impresiones
                            ul.bg-gray-300.w-full(v-show="showedProduct === 4")
                                li(@click="hideNavBar")
                                    router-link(to="/impresiones-clasicas-c") Clasicas
                                li(@click="hideNavBar")
                                    router-link(to="/impresiones-polaroid-d") Polaroid
                        router-link(to="/giftbox")
                            li.bg-menu(@click="hideNavBar") Gift Box
                    li.bg-gray-500.uppercase.w-full(@click="hideNavBar" class="lg:bg-transparent")
                        router-link(to="/promociones") Promociones
                    li.bg-gray-500.uppercase.w-full(@click="hideNavBar" class="lg:bg-transparent") PICTURE BLOG
                    li.bg-gray-500.uppercase.w-full(@click="hideNavBar" class="lg:bg-transparent") Regalos
            .hidden(v-show="showedMenu === 1" class="lg:flex")
                ul.text-white(class="lg:block lg:w-2/12")
                    li.navProduct(@click="setProduct(1)" :class="showedProduct === 1 ? 'bg-selectedMenu' : 'bg-menu'")
                        span.w-full Photobooks
                    li.navProduct(@click="setProduct(2)" :class="showedProduct === 2 ? 'bg-selectedMenu' : 'bg-menu'")
                        span.w-full Foto Album
                    li.navProduct(@click="setProduct(3)" :class="showedProduct === 3 ? 'bg-selectedMenu' : 'bg-menu'")
                        span.w-full Home Decor
                    li.navProduct(@click="setProduct(4)" :class="showedProduct === 4 ? 'bg-selectedMenu' : 'bg-menu'")
                        span.w-full Impresiones
                    router-link(to="/giftbox")
                        li.navProduct.bg-menu(@click="hideNavBar") Gift Box
                .cajitas(class="w-10/12")
                    ul.navSubProduct(v-show="showedProduct === 1" class="lg:grid-cols-2")
                        li(@click="hideNavBar")
                            router-link(to="/photobook-clasico-d")
                                hover-card-underline(image="https://pictureperu.com/web/menu/TAPA%20CLASICO.jpg" titulo="CLASICO")
                        li(@click="hideNavBar")
                            router-link(to="/photobook-tradicional-d")
                                hover-card-underline(image="https://pictureperu.com/web/menu/TAPA%20TRADICIONAL.jpg" titulo="TRADICIONAL")
                        li(@click="hideNavBar")
                            router-link(to="/photobook-premium-d")
                                hover-card-underline(image="https://pictureperu.com/web/menu/TAPA%20PREMIUM.jpg" titulo="PREMIUM")
                        li(@click="hideNavBar({name: 'withDesigns'})")
                            router-link(to="/photobook-deluxe-d")
                                hover-card-underline(image="https://pictureperu.com/web/menu/TAPA%20DELUXE.jpg" titulo="DELUXE")
                    ul.navSubProduct(v-show="showedProduct === 2" class="lg:grid-cols-2")
                        li(@click="hideNavBar")
                            router-link(to="/album-anillado-d")
                                hover-card-underline(image="https://pictureperu.com/web/menu/FOTO%20ANILLADO.jpg" titulo="ANILLADO")
                        li(@click="hideNavBar")
                            router-link(to="/album-tapa-dura-d")
                                hover-card-underline(image="https://pictureperu.com/web/menu/FOTO%20TAPA%20DURA.jpg" titulo="TAPA DURA")
                    ul.navSubProduct(v-show="showedProduct === 3" class="lg:grid-cols-3")
                        li(@click="hideNavBar")
                            router-link(to="/decor-tazas-d")
                                hover-card-underline(image="https://pictureperu.com/web/menu/FOTO%20TAZA.jpg" titulo="TAZAS")
                        li(@click="hideNavBar({name: 'withDesigns'})")
                            router-link(to="/decor-foto-cuadros-d")
                                hover-card-underline(image="https://pictureperu.com/web/menu/FOTO%20CUADRO.jpg" titulo="FOTO CUADROS")
                        li(@click="hideNavBar")
                            router-link(to="/decor-foto-calendarios-d")
                                hover-card-underline(image="https://pictureperu.com/web/menu/FOTO%20CALENDARIO.jpg" titulo="FOTO CALENDARIOS")
                    ul.navSubProduct(v-show="showedProduct === 4" class="lg:grid-cols-2")
                        li(@click="hideNavBar")
                            router-link(to="/impresiones-clasicas-c")
                                hover-card-underline(image="https://pictureperu.com/web/menu/FOTOS%20JUMBO.jpg" titulo="CLASICAS")
                        li(@click="hideNavBar")
                            router-link(to="/impresiones-polaroid-d")
                                hover-card-underline(image="https://pictureperu.com/web/menu/FOTO%20POLAROID.jpg" titulo="POLAROID")
</template>

<script>
    import HoverCardUnderline from "../components/organos/HoverCardUnderline";
    import { mapState, mapMutations } from  'vuex'
    export default {
        name: "NavBar",
        data(){
            return {}
        },
        components: {HoverCardUnderline},
        computed: {
            ...mapState(['showedProduct', 'showedMenu', 'screenWidth', 'showOptions'])
        },
        methods:{
            ...mapMutations(['hideNavBar', 'toggleMenu', 'toggleShowOptions']),
            setProduct(idProduct){
                this.$store.commit('showProduct', {
                    idProduct
                })
            },
            refreshWidth(e){
                if (e.isTrusted){
                    this.$store.commit('setScreenWidth', {});
                }
            }
        },
        created() {
            window.addEventListener("resize", this.refreshWidth);
        },
        destroyed() {
            window.removeEventListener("resize", this.refreshWidth);
        },
    }
</script>
<style lang="sass" scoped>
    /*.router-link-exact-active
        color: #42b983
        background-color: blue*/
    .cajitas
        height: 460px
    .logo
        max-width: 201px
</style>